import Vue from 'vue'
import Router from 'vue-router'


const index = (resolve) => {
  import('@/components/index').then((module) => {
    resolve(module)
  })
}


const doctor_index = (resolve) => {
  import('@/components/Doctor/index').then((module) => {
    resolve(module)
  })
}

const doctor = (resolve) => {
  import('@/components/Doctor/doctor').then((module) => {
    resolve(module)
  })
}


const child_doctor = (resolve) => {
  import('@/components/Doctor/child_doctor').then((module) => {
    resolve(module)
  })
}

const p_doctor = (resolve) => {
  import('@/components/Doctor/p_doctor').then((module) => {
    resolve(module)
  })
}

const os_doctor = (resolve) => {
  import('@/components/Doctor/os_doctor').then((module) => {
    resolve(module)
  })
}

const Appoint= (resolve) => {
  import('@/components/Appoint/Index').then((module) => {
    resolve(module)
  })
}

const hospital = (resolve) => {
  import('@/components/Hospital/hospital').then((module) => {
    resolve(module)
  })
}

const hospital_appoint = (resolve) => {
  import('@/components/Appoint/hospital_appoint').then((module) => {
    resolve(module)
  })
}

const doctor_detail= (resolve) => {
  import('@/components/Doctor/doctor_detail').then((module) => {
    resolve(module)
  })
}

const h_doctor_detail= (resolve) => {
  import('@/components/Doctor/h_doctor_detail').then((module) => {
    resolve(module)
  })
}

const userList = (resolve) => {
  import('@/components/UserCenter/userList').then((module) => {
    resolve(module)

  })
}

const edit= (resolve) => {
  import('@/components/UserCenter/edit').then((module) => {
    resolve(module)

  })
}

const Avatar_edit = (resolve) => {
  import('@/components/UserCenter/Avatar_edit').then((module) => {
    resolve(module)

  })
}

Router.prototype.go = function () {
  this.isBack = true
  window.history.go(-1)

}

Vue.use(Router)
//Vue.use(VueResource)
//Vue.use(axios)
//Vue.http.options.emulateJSON = true
export default new Router({
 /* mode:'history',*/
  routes: [

       {
        path: '/',
        /*query:{openId：1314324234,headimgurl=fjewjfollll&nickName=%C6%F8%C1%F},*/
        component:doctor,
       },

/*       {
        path:'/',
        components:index
       },*/

  /*
       {
          path: '/',
          component:doctor_index,
          redirect:'/doctor_index/doctor',
          children:[
              {
                path:'doctor',
                name:'doctor',
                component:doctor,
              },

            ],
        },
  */

    {
      path: '/p_doctor',
      component: p_doctor
    },
    {
      path: '/os_doctor',
      component: os_doctor
    },
     {
      path: '/child_doctor',
      component:child_doctor
    },

    {
      path:'/doctor_detail',
      name:'doctor_detail',
      component:doctor_detail,
    },
    {
      path:'/h_doctor_detail',
      name:'h_doctor_detail',
      component:h_doctor_detail,
    },


    {
      path: '/userList',
      component: userList,
    },

    {
      path: '/edit',
      name:'edit',
      component: edit,
     },

     {
      path: '/Avatar_edit',
      component:Avatar_edit,
     },

      {
        path: '/appoint',
        component: Appoint,
        redirect:'/appoint/hospital_appoint',
        children:[

            {
              path:'hospital_appoint',
              name:'hospital_appoint',
              component:hospital_appoint,
            },
           
          ],

      },


     {
      path: '/hospital',
      component:hospital,
     },




  ]
})



